import FileItem from "jx/editor/editionitems/FileItem";
export default class PSD extends FileItem {
    constructor(dataObject) {
        dataObject.data.asset.type = "PSD";
        super(Object.assign({
            icoSymbol: "file-powerpoint-o",
            actions: ["download"],
            informations: ["asset"],
            backgroundColor: "#99dd33"
        }, dataObject));
    }
}
